*{
    margin: 0;
    padding: 0;
    font-size: 62.5px;
}
.header-wrap{
    background-color: tomato;
    width: 99%;
    height: 1.3rem;
    color: white;
    padding: 5px;
}
.header-content{   
  
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    color: white;
    font-family :Verdana, Geneva, Tahoma, sans-serif;
    height: 100%;
}
.header-content h1{
    font-size: 0.8rem;
    
    vertical-align:middle;
    color: white;
    margin-left: 0.2rem;
}
.searchIcon{
    margin-left: 0.2rem;
}

@media screen and (max-device-width: 640px) 
{
    .header-wrap{
        background-color: white;
       
    }
    .header-content h1{
    font-size: 0.4;
    color:aliceblue;
    }
}